@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap);
:root{
  font-family: 'Poppins','sans-serif';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;

  /* Global Variables */
  --PrimaryColor : #6FCF97;
  --Black : #333333;
  --Grey : #BDBDBD;
  --White : #ffffff;

  /* BreakPoints */
  --Phone: 480px;
  --Tablet: 768px;
}

body {
  margin: 0;
}

h1, h2, h3, h4, h5{
  font-weight: 400;
  margin: 0;
}

h1 {
  font-size: 0.9rem;
  color : #BDBDBD;
  color : var(--Grey);

}

h2 {
  font-size: 1.2rem;
  font-weight: 500;
}

.card{
  padding: 1.5rem;
  margin-left: 1rem;
  margin-right: 1rem;
  margin-top: 1rem;
  background-color: #6FCF97;
  background-color: var(--PrimaryColor);
  border-radius: 5px;
  color: #ffffff;
  color: var(--White);
  line-height: 120%;
}


button{
  text-decoration: none;
  border: none;
  border-radius: 5px;
  background-color: #6FCF97;
  background-color: var(--PrimaryColor);
  color: #ffffff;
  color: var(--White);
  height: 2.5rem;
  width: 9rem;
  font-size: 1rem;
  font-family: "Poppins";
  cursor:pointer
}
.active_class{
  color: #6FCF97;
  color: var(--PrimaryColor);
}

nav{
  background-color:#fff !important;
  margin-bottom:0px;
  margin-left: 2rem;
  margin-right: 2rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
  align-items: center;
  display: flex;
  justify-content: space-between;
}

nav a {
 font-size: 1rem;
 display: flex;
 align-items: center;
 text-decoration: none;
 color: var(--Black);
}

nav a .title{
  padding-left: 1rem;
  line-height: 1.2;
} 

nav  ul{
  display: flex;
  list-style-type: none;
  padding-right: 6rem;
}

.links{
  font-size: 1.1rem;
  margin-left: 4rem;
  padding:5px;
  border-radius:4px;
  cursor:pointer;
}

.nav-brand{ 
 color:blue;
 font-size:1.9rem;
} 

.logo{
  height:50px;
  width: 50px;
}

@media(max-width: 480px){
  
   ul {
    z-index: 10;
    position: fixed;
    top: 0;
    right: 0;
    padding-right: 0 !important;
    margin: 0;
    height: 100vh;
    width: 100vw;
    background-color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: -webkit-clip-path cubic-bezier(0.785, 0.135, 0.15, 0.86) 0.5s;
    transition: clip-path cubic-bezier(0.785, 0.135, 0.15, 0.86) 0.5s;
    transition: clip-path cubic-bezier(0.785, 0.135, 0.15, 0.86) 0.5s, -webkit-clip-path cubic-bezier(0.785, 0.135, 0.15, 0.86) 0.5s;
    -webkit-clip-path: circle(0 at 89% 7%);
            clip-path: circle(0 at 89% 7%);
  }
  
  .active{
    -webkit-clip-path: circle(100%);
            clip-path: circle(100%);
  }

  li {
    min-width: 4rem;
    margin-left: 0px !important;
    margin-bottom: 0.8rem;
    font-size: 1.3rem !important;
  }

  nav {
    margin-left: 1rem;
    margin-right: 1rem;
  }
  
  .title{
    display: none;
  }

  .nav-menu-btn {
    z-index: 11;
    height: 0.8rem;
    width: 2rem;
    margin-right: 0rem;
    position: relative; }
    .nav-menu-btn > div {
      height: 3px;
      border-width: 1px;
      width: 100%;
      background-color: var(--Black);
      position: absolute;
      transition: all 0.2s cubic-bezier(0.1, 0.82, 0.76, 0.965); }
      
      .nav-menu-btn > div:first-of-type {
      top: 0; }
    
      .nav-menu-btn > div:last-of-type {
      bottom: 0; }
  
      .nav-menu-btn .nav-menu:checked + .first {
        background-color: var(--Black);
        -webkit-transform: rotate(45deg);
                transform: rotate(45deg);
        top: 5px; }
  
      .nav-menu-btn .nav-menu:checked + .first + .second {
        background-color: var(--Black);
        -webkit-transform: rotate(-45deg);
                transform: rotate(-45deg);
        bottom: 5px; }
  
      .nav-menu-btn .nav-menu {
        width: 100%;
        height: 100%;
        position: relative;
        cursor: pointer;
        z-index: 2;
        -webkit-touch-callout: none;
        -webkit-tap-highlight-color: none;
        -webkit-user-select: none;
            -ms-user-select: none;
                user-select: none;
        position: absolute;
        opacity: 0;
      }
}

@media (min-width: 480px) {
  .nav-menu-btn{
    display: none;
  }
}


.showslider{
    height:500px;
    overflow:hidden;
    margin-left: 1rem;
    margin-right: 1rem;
    border-radius: 5px;
    position: relative;
}

.navigation{
    position:absolute;
    bottom:2%;
    left: 50%;
    /* margin-left: -50%; */
    -webkit-transform:translateX(-50%);
            transform:translateX(-50%);
    display:flex;
}
.bar{
    width: 10px;
    height: 10px;
    border: 2px solid var(--White);
    margin: 10px;
    border-radius: 10px; 
    transition: 0.5s;
    cursor:pointer;
}
.bar:hover{
    background: var(--White) ;
}
input[name="a"]{
    position:absolute;
    visibility:hidden;
}
.slides{
   width:500%;
   height:100%;
   display:flex;
}
.slide{
   width:20%;
   transition:0.6s;
}
.slide img{
    width: 100%;
    height: 500px;
}
#a1:checked ~ .s1{
    margin-left:0%;
}

#a2:checked ~ .s1{
    margin-left:-20%;
}
#a3:checked ~ .s1{
    margin-left:-40%;
}

@media(max-width: 480px){
  .slide img{
    width: auto;
    height: 100%;
  }
} 

